@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

html, body, #root {
  height: 100%;
}

body {
  overscroll-behavior: none;
}

#app {
  position: absolute;
  display: flex;
  flex-direction: column;

  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

#app-bar {
  display: inline-block;
  padding: 0 8px;

  z-index: 1300;
  background-color: #FFF;
}

#function-input {
  margin-left: 3px;
  margin-right: 75px;

  overflow: hidden;
  flex-grow: 1;

  font-size: 18px;
  font-family: 'KaTeX_Main';
}

#help-button {
  position: absolute;
  right: 0;
}

@media (max-width: 480px) {
    #function-input {
        font-size: 16px;
    }
}

#function-plot {
  position: absolute;

  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  background: #222;
  overflow: hidden;
}

#function-plot > canvas {
  position: absolute;

  width: 100%;
  height: 100%;

  z-index: 10;
}

#function-plot > canvas.axes {
  z-index: 11;
  pointer-events: none;
  -webkit-user-select: none;
          user-select: none;
}

#fallback-text {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  text-align: center;
  font-size: 40px;

  background-color: hsl(200, 15%, 10%);
  color: white;

  font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;

  z-index: 1;
}

#fallback-text .small {
    font-size: 16px;
    margin: 0 30px;
    margin-top: 15px;
}

.drawer-content {
  overflow-x: hidden;
  background-color: white;
}

.MuiList-root {
    background-color: white;
    opacity: 1;
    transition: opacity 0.2s ease-in-out;
}

@media (max-width: 480px) {
    .drawer-content {
        background-color: unset;
    }

    .MuiList-root {
        background-color: unset;
    }

    .control-panel.changing .MuiList-root:not(.slider-list) {
        opacity: 0;
    }
}

.control-panel {
  width: 330px;
  height: 100% !important;
  resize: horizontal;
  background-color: white;
}


/* Media Queries */

@media (max-width: 480px) {
  .control-panel {
    width: 100%;
  }

  .MuiBackdrop-root {
   background-color: unset !important;
  }

  .control-panel.changing {
    background-color: rgba(0, 0, 0, 0);
  }
}

.editable-value {
	color: #777;
	padding: 0;
	border: 0;

	transition: color 0.1s;
}

.editable-value:hover {
	color: #444;
}

.editable-value.focus {
	color: black;
}

.editable-value.invalid {
	color: red;
}

.slider-base {
	width: 100%;
	display: flex;
	white-space: nowrap;

	margin: 16px 0;
	margin-bottom: 24px;
	padding: 0 0 0 25px;

	box-sizing: border-box;
}

.slider {
	flex-grow: 1;
}

.slider-base .delete-icon {
	padding: 0;
	margin: auto 8px;
	width: 40px;
	height: 40px;
}

/*** Info Row ***/
.slider .info-row {
	white-space: nowrap;
	display: flex;

	width: 100%;
	height: 20px;
}

.slider .info-row {
	font-size: 16px;
	font-weight: 500;
	font-family: 'KaTeX_Main', 'Roboto', 'Helvetica', 'Arial', sans-serif;
	line-height: 20px;
}

.slider .info-row .editable-value {
	color: black;
}

.slider .info-row .editable-value.name-italic {
    font-style: italic;
    font-family: 'KaTeX_Math';
    margin-right: 4px;
}

.slider .info-row .editable-value.invalid {
	color: red;
}

.slider .info-row .name {
	margin-right: 3px;
}

.slider .info-row .value {
	margin-left: 3px;
}

/*** Main Row ***/
.slider .main-row {
	white-space: nowrap;
	display: flex;

	width: 100%;
	height: 30px;
}

.slider .main-row .editable-value {
	font-size: 16px;
	font-family: 'KaTeX_Main', 'Roboto', 'Helvetica', 'Arial', sans-serif;
	line-height: 30px;
}

.slider .main-row .main-slider {
	margin: auto 10px;
	padding: 0;
	flex-grow: 1;
}

@media (max-width: 480px) {
    .control-panel.changing .slider-wrapper {
        box-shadow: 1px 1px 4px hsla(200, 10%, 10%, 0.3);
    }

    .slider .info-row {
        padding-top: 8px;
    }
}


div.variable-slider {
  width: 90%;
}

@media (max-width: 480px) {
    .control-panel.changing .variable-adder {
        opacity: 0;
    }

    .control-panel.changing .MuiListSubheader-root {
        opacity: 0;
    }
}

#function-editor {
  margin: 20px;
  margin-top: 0;
}

@media (max-width: 480px) {
    #function-editor .resize-helper {
        display: none;
    }
}

.help-panel {
  max-width: 580px;
  width: 100vw;
  height: 100% !important;
}

#help-text {
  margin: 30px;
}

#help-text p,li {
  font-size: 15px;
  line-height: 1.5;

  font-family: 'Roboto', 'Open Sans', sans-serif;

  color: hsl(200, 10%, 10%);

  margin-top: 0;
}

#help-text li {
  margin-bottom: 10px;
}

#help-text h1, h2, h3 {
  font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
}

#help-text h1 {
  font-weight: 300;
  font-size: 32px;
  color: hsl(200, 10%, 25%);

  margin-top: 0;
  margin-bottom: 10px;

  display: flex;
  flex-direction: row;
  align-items: center;
}

#help-text h1 .logo {
    width: 60px;
    margin-right: 18px;
}

#help-text h1 .text {
    display: flex;
    flex-direction: column;
}

#help-text h1 .text .subtitle {
    font-size: 14px;
    color: hsl(213, 10%, 30%);
    margin-left: 3px;
}

#help-text h2 {
  font-weight: 500;
  font-size: 22px;
  line-height: 1.25;
  color: hsl(200, 10%, 15%);

  margin-top: 40px;
  margin-bottom: 10px;
}

#help-text h3 {
  font-weight: 400;
  font-size: 13px;
  color: hsl(200, 10%, 30%);

  text-transform: uppercase;
  letter-spacing: 0.07em;

  margin-top: 20px;
  margin-bottom: 3px;
}

.help-indent {
  margin-left: 20px;
}

#help-text form {
    margin-top: 10px;
}

/* Media Queries */
@media (max-width: 600px) {
    #help-text h1 {
        font-size: 22px;
    }

    #help-text h1 .logo {
        width: 50px;
        margin-right: 15px;
    }

    #help-text h2 {
        font-size: 18px;
    }

    #help-text p,li {
        font-size: 14px;
    }
}

